<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "constructionStatisticsComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "supplier_Short_Name", label: "承攬商名稱", width: "150" },
        { id: "floor", label: "主區域", width: "150" },
        { id: "area_Name", label: "次區域", width: "150" },
        { id: "work_Content", label: "施工內容", width: "150" },
        { id: "engineer", label: "負責工程師", width: "150" },
        { id: "duty_Name", label: "廠商施工負責人", width: "150" },
        { id: "enter_Date", label: "入廠時間", width: "150" },
        { id: "Retired_Date", label: "出廠時間", width: "150" },
      ],
      factory: {},
      allWorkTypes: [],
      normal: {},
      permanent: {},
      todaySummary: {},
      data: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    // API取得資料
    async getQrySummary(params) {
      this.$store.state.locationImg = null;
      const res = await this.$store.dispatch(
        "constructionMgmQrySummary",
        params
      );
      const data = res.data.data;
      this.allWorkTypes = data.allWorkTypes;
      this.normal = data.normal;
      this.permanent = data.permanent;
      this.todaySummary = data.todaySummary;
      this.$store.commit("SET_LOCATION_IMG", data.locationImg);
    },
    goBack() {
      this.$router.go(-1);
    },
    changePage(page) {
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
      this.$store.state.currentPage = page;
    },
    async workTypeDetail(item) {
      const obj = {
        work_type_pk: item.work_Type_Pk,
        NorthOrSouth: this.getNorthOrSouth(this.factory.e_Description),
        Location_Id: this.getLocation(this.factory.location_Id),
      };
      const params = this.getURLNewParams(obj);
      this.data = await this.$store.dispatch(
        "constructionMgmQryWorkTypeDetail",
        params
      );
      this.data.forEach((e, index) => {
        e.index = index + 1;
        e.engineer = `${e.eng_Name}(ext=${e.eng_Ext_No})`;
      });
      this.tableData = JSON.parse(JSON.stringify(this.data));
      this.tableList = this.tableData.slice(0, 10);
    },
    getNorthOrSouth(param) {
      if (param === "north" || param === "south") {
        return param;
      } else {
        return 1;
      }
    },
    getLocation(param) {
      if (param === "north" || param === "south") {
        return "LOCATION_ID";
      } else {
        return param;
      }
    },
    constructionArea() {
      const obj = {
        description: this.factory.description,
        NorthOrSouth: this.getNorthOrSouth(this.factory.e_Description),
        Location_Id: this.getLocation(this.factory.location_Id),
      };
      this.$router.push({
        name: "constructionArea",
        params: { params: JSON.stringify(obj) },
      });
    },
    constructionRecords() {
      const obj = {
        description: this.factory.description,
        NorthOrSouth: this.getNorthOrSouth(this.factory.e_Description),
        Location_Id: this.getLocation(this.factory.location_Id),
      };
      this.$router.push({
        name: "constructionRecords",
        params: { params: JSON.stringify(obj) },
      });
    },
    dataQuery() {
      const obj = {
        description: this.factory.description,
        NorthOrSouth: this.getNorthOrSouth(this.factory.e_Description),
        Location_Id: this.getLocation(this.factory.location_Id),
      };
      this.$router.push({
        name: "dataQuery",
        params: { params: JSON.stringify(obj) },
      });
    },
  },
  async mounted() {
    this.factory = JSON.parse(this.$route.params.detail);
    // breadcrumb麵包屑更新
    this.$route.meta.breadcrumb[1].name = this.factory.description;
    const obj = {
      NorthOrSouth: this.getNorthOrSouth(this.factory.e_Description),
      Location_Id: this.getLocation(this.factory.location_Id),
    };
    const params = this.getURLNewParams(obj);
    await this.getQrySummary(params);
    this.$store.state.currentPage = 1;
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./constructionStatistics.html"></template>
